@import "variables.scss";

* {
  margin: 0;
  padding: 0;
}


body {
  min-width: 320px;

  font-family: $font-family;
  background: $bg;

  &.overfl {
    overflow: hidden;
    height: 100vh;
  }
}

.start-page {
  padding: 30px;

  &-logo {
    display: block;
    margin-bottom: 30px;
  }

  ul {
    display: inline-block;
    vertical-align: top;
    padding-right: 60px;

    li {
      padding: 5px 0;

      b {
        font-size: 20px;
      }

      a {
        font-size: 16px;
        line-height: 140%;
        color: $black;

        &:hover {
          //color: $active;
        }
      }
    }
  }
}

div,
span,
a,
input,
textarea,
select,
button,
.button {
  &:focus {
    outline: none;
  }
}

input,
textarea, {
  border-radius: 0!important;
  box-shadow: none!important;
}

ul {
  li {
    list-style: none;
  }
}

a {
  cursor: pointer;
  transition: 0.3s;

  &:hover {
    text-decoration: none;
  }
}

svg {
  use {
    transition: 0.3s;
  }
}

h1 {
  margin-bottom: 50px;

  font-family: $font-family3;
  font-size: 46px;
  line-height: 110%;
  color: $gray3;
}

h2 {
  margin-bottom: 50px;

  font-family: $font-family3;

  font-weight: 600;
  font-size: 38px;
  line-height: 100%;
  color: $gray3;
}

h3 {
  margin-bottom: 25px;

  font-family: $font-family3;

  font-weight: 700;
  color: $gray3;
  font-size: 26px;
  line-height: 110%;
}

h4 {
  margin-bottom: 15px;

  font-family: $font-family3;

  font-weight: 600;
  font-size: 20px;
  line-height: 140%;
  color: $black2;
}


.container {
  max-width: 100%;

  @media screen and(min-width: 1440px) {
    max-width: 1410px;
  }
}

.button {
  display: inline-block;
  padding: 10px 30px;

  box-shadow: none;
  border: none;

  &-center {
    margin: 35px 0;

    text-align: center;
  }

  &-group {
    .button {
      margin: 0 10px;
    }
  }

  &-white {
    padding: 15px 45px;

    text-transform: uppercase;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.03em;
    color: $gray3;
    background: $white;

    svg {
      width: 22px;
      height: 10px;
      margin-left: 10px;

      use {
        color: $gray3;
      }
    }

    &:hover {
      background: $active;
      color: $white;

      svg {
        use {
          color: $white;
        }
      }
    }
  }

  &-secondary {
    padding: 15px 55px;

    text-transform: uppercase;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.03em;
    color: $active;
    background: $color10;

    &:hover {
      background: $active;
      color: $white;
    }
  }

  &-primary {
    padding: 15px 55px;

    text-transform: uppercase;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.03em;
    color: $white;
    background: $color2;

    svg {
      width: 12px;
      height: 8px;
      margin-left: 10px;

      use {
        color: $white;
      }
    }

    &:hover {
      color: $active;
      background: $color10;

      svg {
        use {
          color: $active;
        }
      }
    }
  }

  &-gray {
    padding: 15px 55px;

    letter-spacing: 0.03em;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    color: $white;
    background: $gray8;

    &:hover {
      color: $active;
      background: $color10;
    }
  }

  &-close {
    display: flex!important;
    align-items: center;
    justify-content: center;
    padding: 15px 55px;

    font-size: 16px;
    line-height: 150%;
    color: $black;
    font-weight: 400;

    text-align: center;

    svg {
      width: 24px;
      height: 24px;
      margin-right: 5px;

      use {
        color: $black;
      }
    }

    &:hover {
      color: $active;

      svg {
        use {
          color: $active;
        }
      }
    }
  }

  &-bord {
    padding: 15px 15px;

    letter-spacing: 0.03em;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    color: $gray8;
    border: 1px solid $gray8;

    &:hover {
      color: $gray3;
      border-color: $gray3;
    }

    .cart-order & {
      padding: 15px 55px;
    }
  }

  &-bord2 {
    padding: 15px 55px;

    letter-spacing: 0.03em;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    color: $active;
    border: 1px solid $active;

    &:hover {
      color: $gray3;
      border-color: $gray3;
    }
  }

  &-bord3 {
    padding: 15px 55px;

    letter-spacing: 0.03em;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    color: $gray3;
    border: 1px solid $gray3;

    &:hover {
      color: $white;
      background: $gray3;
    }
  }

  &-link {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 15px;

    font-weight: 600;
    font-size: 13px;
    line-height: 16px;
    color: $gray3!important;
    text-transform: uppercase;

    img {
      width: 13px;
      height: 10px;
      margin-left: 6px;
    }


  }

  &-mob {
    display: none;

    text-align: center;
  }
}

.header {
  background: $white;

  &__inner {

  }

  &__top {
    padding: 10px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &-left,
    &-right {
      width: 40%;
    }

    &-right {
      text-align: right;
    }

    &-link {
      margin-left: 50px;

      font-size: 16px;
      line-height: 160%;
      color: $black2;

      &:hover {
        color: $active;
      }
    }
  }

  &__phone {
    display: inline-block;
    margin-right: 30px;

    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: $black2;

    &:hover {
      color: $active;
    }
  }

  &__descr {
    display: inline-block;

    font-size: 16px;
    line-height: 160%;
    color: $black2;
  }

  &__cart {
    display: inline-flex;
    align-items: center;
    padding-left: 34px;
    margin-left: 47px;

    font-size: 16px;
    line-height: 160%;
    color: $black2;

    position: relative;

    transition: 0.3s;
    cursor: pointer;

    svg {
      width: 24px;
      height: 24px;

      position: absolute;
      top: 0;
      left: 0;

      use {
        color: $black2;
      }
    }

    &:hover {
      color: $active;

      svg {
        use {
          color: $active;
        }
      }
    }
  }

  &__search {
    display: inline-block;
    margin-left: 46px;

    position: relative;

    &-lnk {
      padding-left: 33px;

      font-size: 16px;
      line-height: 160%;
      color: $black2;

      position: relative;

      transition: 0.3s;
      cursor: pointer;

      svg {
        width: 24px;
        height: 24px;

        position: absolute;
        top: 0;
        left: 0;

        use {
          color: $black2;

          transition: 0.3s;
        }
      }

      &:hover {
        color: $active;

        svg {
          use {
            color: $active;
          }
        }
      }
    }

    &-in {
      width: 325px;
      display: none;

      position: absolute;
      right: 0;
      bottom: -7px;
      z-index: 2;

      .open & {
        display: block;
      }

      input {
        width: 100%;
        height: 40px;
        padding: 5px 40px 5px 10px;

        border: none;
        border-bottom: 1px solid $gray;
        font-size: 14px;
        line-height: 160%;
        box-shadow: none;

        &::-webkit-input-placeholder { color: $gray2; }
        &:-ms-input-placeholder { color: $gray2; }
        &::placeholder { color: $gray2; }
      }
    }

    &-btn {
      display: inline-block;
      width: 30px;
      height: 25px;

      position: absolute;
      top: 8px;
      right: 0;
      z-index: 2;

      background: transparent;
      border: none;
      cursor: pointer;

      svg {
        width: 24px;
        height: 24px;

        use {
          color: $black2;
        }
      }

      &:hover {
        svg {
          use {
            color: $active;
          }
        }
      }
    }
  }

  &__menu {
    position: relative;

    ul {
      margin-bottom: 0;
      display: flex;
      align-items: center;
      justify-content: space-between;

      & > li {
        display: inline-block;
        height: 70px;

        &.mob {
          display: none;
        }

        &.desct {
          display: inline-block;
        }

        @media screen and (min-width: 991px) {
          &.drop {
            & > a {
              padding-right: 20px;

              &:before {
                content: '';
                width: 20px;
                height: 20px;
                display: block;

                position: absolute;
                top: 0;
                bottom: 0;
                right: 0;
                margin: auto;

                background-image: url("../images/svg/arrow.svg");
                background-repeat: no-repeat;
                background-position: center;
                background-size: 20px 20px;
                transition: 0.3s;
              }
            }
          }
        }

        & > a {
          height: 100%;

          font-weight: 600;
          font-size: 14px;
          line-height: 17px;
          color: $black2;
          display: flex;
          align-items: center;
          letter-spacing: 0.03em;
          text-transform: uppercase;

          position: relative;

          &:after {
            content: '';
            width: 100%;
            height: 2px;
            display: block;
            opacity: 0;

            position: absolute;
            bottom: 0;
            left: 0;
            z-index: 2;

            background: $active;
            transition: 0.3s;
          }



          .img {
            margin-right: 10px;
          }

          & + .arr {
            display: none;
            width: 20px;
            height: 20px;
            vertical-align: middle;

            position: absolute;
            top: 0;
            bottom: 0;
            right: 0;
            margin: auto;

            transition: 0.3s;

            svg {
              width: 20px;
              height: 20px;

              use {
                color: $black2;
              }
            }
          }
        }

        @media screen and (min-width: 991px) {
          &:hover {
            & > a {
              color: $active;

              &:before {
                background-image: url("../images/svg/arrow-act.svg");
                transform: rotate(180deg);
              }

              & + .arr {
                transform: rotate(180deg);
              }

              &:after {
                opacity: 1;
              }

              svg {
                use {
                  color: $active;
                }
              }
            }
          }

          &.drop {
            &:hover {
              .drop-bl {
                display: block;
              }
            }
          }
        }

        .drop-bl {
          width: 100%;
          padding: 30px 40px;
          display: none;

          position: absolute;
          top: 70px;
          left: 0;
          z-index: 10;

          background: $bg;

          &__img {
            width: 307px;
            margin-left: 20px;

            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
          }

          &__inner {
            display: flex;
            flex-wrap: wrap;
          }

          &__item {
            padding: 0 10px;
            flex-basis: 0;
            flex-grow: 1;
            max-width: 100%;

            &-title {
              margin-bottom: 23px;

              font-weight: 500;
              font-size: 18px;
              line-height: 22px;
              color: $black2;
            }

            &-lnk {
              display: block;
              margin-top: 22px;

              font-weight: 500;
              font-size: 18px;
              line-height: 22px;
              color: $black2;

              &:hover {
               color: $active;
              }
            }

            ul {
              display: block;

              & + .drop-bl__item-lnk {
                margin-top: 42px;
              }

              & > li {
                display: block;

                height: auto;
                margin-bottom: 13px;

                &:last-of-type {
                  margin-bottom: 0;
                }

                & > a {
                  height: auto;

                  font-weight: 400;
                  font-size: 14px;
                  line-height: 140%;
                  color: $black2;
                  display: flex;
                  align-items: center;
                  letter-spacing: 0.01em;
                  text-transform: inherit;

                  &:before {
                    display: none!important;
                  }

                  &:after {
                    display: none!important;
                  }

                  &:hover {
                    color: $active;
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  &-mob {
    height: 75px;
    display: none;

    background: $white;

    &-close {
      display: none;
    }

    &__tel {
      display: none;

      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      color: $black2;
    }

    &__inner {
      height: 75px;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &__toggler {
      img {
        width: 32px;
        height: 32px;
      }
    }

    &__cart {
      display: inline-block;

      img {
        width: 28px;
        height: 28px;
      }
    }

    &__logo {
      display: block;

      img {
        max-width: 140px;
      }
    }
  }
}

.banner {
  &__inner {
    position: relative;
  }

  &__call {
    display: inline-block;
    padding-right: 60px;

    position: absolute;
    bottom: 70px;
    right: 53px;
    z-index: 3;

    &-tx {
      font-weight: 500;
      font-size: 12px;
      line-height: 15px;
      display: flex;
      align-items: center;
      text-align: right;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      color: $gray3;
    }

    &-icon {
      width: 40px;
      height: 40px;
      display: inline-flex;
      align-items: center;
      justify-content: center;

      position: absolute;
      right: 0;
      top: -15px;
      z-index: 2;

      border-radius: 100px;
      border: 1px solid $gray3;
      transition: 0.3s;

      svg {
        width: 18px;
        height: 18px;

        use {
          color: $gray3;
          transition: 0.3s;
        }
      }
    }

    &:hover {
      .banner__call-icon {
        transform: scale(1.1);

        svg {
          transform: scale(1.1);
        }
      }
    }
  }

  &__slider {
    &-item {
      height: 480px;

      border-radius: 0;
      overflow: hidden;

      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }

    &-inner {
      width: 70%;
      padding: 85px 0;
    }

    &-title {
      margin-bottom: 30px;

      font-weight: 600;
      font-size: 65px;
      line-height: 105%;
      color: $gray3;
      font-family: $font-family3;
    }


    &-text {
      margin-bottom: 50px;

      font-size: 16px;
      line-height: 160%;
      color: $gray3;
    }
  }

  &-dp {
    margin: 85px 0;

    &__slider {
      &-item {

      }

      &-inner {
        padding: 50px 60px 110px;

        border-radius: 4px;
        overflow: hidden;

        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
      }

      &-title {
        width: 450px;
        margin-bottom: 44px;

        font-weight: 600;
        font-size: 52px;
        line-height: 110%;
        font-family: $font-family3;
        color: $white;

        .banner-dp-tw & {
          color: $color9;
          margin-bottom: 20px;
        }
      }

      &-tx {
        width: 385px;
        margin-bottom: 30px;

        font-size: 16px;
        line-height: 160%;
        color: $gray3;
      }
    }
  }
}

.slick {
  &-slide {
    .catalog-min & {
      padding: 0 20px;
    }

    .cart-product__slider-nav & {
      opacity: 0.5;

      &.slick-current.slick-active {
        opacity: 1;
      }
    }
  }

  &-dots {
    margin-bottom: 0;

    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 3;

    .banner__slider & {
      right: 55px;
      top: 50px;
      left: inherit;
      bottom: inherit;
      z-index: 3;
    }

    .cart-product__slider & {
      width: 100%;

      bottom: 13px;
      z-index: 2;

      text-align: center;
      margin: auto;
    }

    .banner-dp__slider & {
      bottom: 33px;
      left: 62px;
    }

    li {
      width: 6px;
      height: 6px;
      display: inline-block;

      background: $white;
      opacity: 0.5;
      cursor: pointer;

      border-radius: 100px;

      .banner__slider & {
        margin-bottom: 23px;
        display: block;
      }

      &.slick-active {
        opacity: 1;
      }

      .cart-product__slider-for & {
        background: $gray2;
        margin: 0 10px;
      }

      .banner-dp__slider & {
        margin-right: 25px;
      }

      button {
        display: none;
      }
    }
  }

  &-arrow {
    display: flex;
    align-items: center;

    cursor: pointer;

    svg {
      width: 28px;
      height: 18px;

      use {
        color: $gray3;
      }
    }

    &:hover {
      svg {
        use {
          color: $active;
        }
      }
    }

    .catalog-min &,
    .article-main__slider &,
    .banner-dp-tw .banner-dp__slider & {
      position: absolute;
      top: -54px;
      right: 0;
      z-index: 3;
    }
  }

  &-prev {
    margin-top: -2px;
    transform: rotate(180deg);

    .catalog-min &,
    .article-main__slider &,
    .banner-dp-tw .banner-dp__slider &  {
      left: inherit;
      right: 65px;
    }

    .article-main__slider & {

    }

    .banner-dp__slider & {
      right: 45px!important;
    }
  }

  &-next {
    .catalog-min & {
      right: 18px;
      top: -56px;
    }

    .article-main__slider & {
      right: 18px;
      top: -56px;
    }

    .banner-dp__slider & {
      top: -56px!important;
    }
  }
}

.category {
 &-min {
   padding: 50px 0;

   &__inner {
    text-align: center;
   }

   &__item {
     display: inline-block;
     vertical-align: middle;
     margin: 0 20px;

     text-align: center;

     &:hover {
       .category-min__title {
         color: $active;
       }

       .category-min__img {
         border-color: $active;
       }
     }
   }

   &__title {
     margin-top: 5px;
     display: block;

     font-size: 16px;
     line-height: 160%;
     color: $black2;
     transition: 0.3s;
   }

   &__img {
     width: 70px;
     height: 70px;
     display: block;
     margin: 0 auto;

     border-radius: 100%;
     overflow: hidden;
     border: 1px solid $gray3;
     transition: 0.3s;

     &-inner {
       width: 100%;
       height: 100%;
       display: block;

       background-size: cover;
       background-position: center;
       background-repeat: no-repeat;

       border: 2px solid $white;
     }
   }
 }
}

.catalog {
  &-min {
    margin: 30px 0 40px;
    overflow: hidden;
  }

  &-main {
    margin: 85px 0;

    &__item {
      height: 280px;
      padding: 30px;
      margin-bottom: 40px;

      position: relative;

      border-radius: 4px;
      overflow: hidden;

      &-bg1 {
        background: $color4;
      }
      &-bg2 {
        background: $color5;
      }
      &-bg3 {
        background: $color6;
      }
      &-bg4 {
        background: $color7;
      }
      &-bg5 {
        background: $color1;
      }
      &-bg6 {
        background: $color8;
      }

      a {
        width: 100%;
        height: 100%;
        display: block;

        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;
      }

      &:hover {
        .catalog-main__arrow {
          opacity: 1;
        }
      }
    }

    &__title {
      margin-bottom: 10px;
      display: block;

      position: relative;
      z-index: 2;

      font-weight: 700;
      font-size: 26px;
      line-height: 110%;
      color: $gray3;
      font-family: $font-family3;
    }

    &__tx {
      display: block;

      position: relative;
      z-index: 2;

      font-weight: 500;
      font-size: 18px;
      line-height: 140%;
      color: $gray3;
    }

    &__img {
      position: absolute;
      bottom: 0;
      right: 0;
      z-index: 1;
    }

    &__arrow {
      display: block;
      opacity: 0;
      transition: 0.3s;

      position: absolute;
      left: 30px;
      bottom: 17px;

      svg {
        width: 25px;
        height: 15px;

        use {
          color: $gray3;
        }
      }
    }
  }

  &__list {
    .catalog-min & {
      margin: 0 -20px;
    }

    &-tile {

    }
  }

  &__item {
    padding: 55px 22px 85px;

    position: relative;

    background: $white;
    overflow: hidden;
    box-shadow: 0px 4px 21px rgba(148, 24, 24, 0.03);
    border-radius: 4px;

    .catalog-page__content & {
      height: 100%;
      margin-bottom: 0;
    }


    @media screen and (min-width: 991px) {
      &-list {
        align-items: center;
        display: flex;
        height: auto!important;
        margin-bottom: 40px;
        padding: 30px 240px 20px 180px;
        min-height: 200px;
