.filter {
  width: 100%;

  &__total {
    width: 152px;
    height: 79px;
    padding: 17px 17px 20px 35px;

    position: absolute;
    right: -123px;
    top: 59px;
    z-index: 30;

    background-image: url("../images/svg/tooltip.svg");
    background-repeat: no-repeat;
    background-position: center;

    &-quantity {
      font-size: 12px;
      line-height: 120%;
      letter-spacing: 0.01em;
      color: $gray9;
    }

    &-all {
      display: inline-block;

      font-size: 12px;
      line-height: 120%;
      letter-spacing: 0.01em;
      color: $active!important;

      &:hover {
        color: $color2!important;
      }
    }
  }

  &__top {
    display: none;

    &-close,
    &-tt {
      display: none;
    }
  }

  &__btn {
    .button {
      margin-bottom: 20px;
      width: 100%!important;

      text-align: center;
    }
  }

  &__top {
    display: none;
  }

  &__item {
    padding: 30px 0;

    position: relative;

    border-top: 1px solid $gray;

    .filter__list & {
      padding: 10px 0;
      border: none;
    }

    &.active {
      .filter__title {
        font-weight: 700;
      }
    }

    &:last-of-type {
      margin-bottom: 20px;
    }
  }

  &__container {
    display: none;
    margin-top: 25px;

    .open & {
      display: block;
    }
  }

  &__title {
    position: relative;

    font-weight: 500;
    font-size: 16px;
    line-height: 160%;
    color: $black2;
    cursor: pointer;

    &:after {
      content: '';
      width: 18px;
      height: 18px;
      display: block;

      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      margin: auto;

      background-repeat: no-repeat;
      background-position: center;
      background-image: url("../images/svg/arrow.svg");
      background-size: 18px 18px;
    }

    &.open {
      &:after {
        transform: rotate(180deg);
      }
    }

  }

  &__range {
    &-item {
      position: absolute;
      top: 75px;

      &:before {
        content: '';
        display: block;

        position: absolute;
        left: 12px;
        top: 8px;

        font-size: 16px;
        line-height: 26px;
        color: $gray8;
        letter-spacing: 0.01em;
      }

      &:nth-of-type(1) {
        left: 0;

        &:before {
          content: 'от';
        }
      }

      &:nth-of-type(2) {
        right: 0;

        &:before {
          content: 'до';
        }
      }
    }

    &.polzunok-container-5 {
      margin: 85px 0 17px;

      box-sizing: border-box;
      outline: none;

      &:after {
        content: "";
        clear: both;
        display: table;
      }

      .ui-slider {
        height: 4px;

        position: relative;

        background: $gray8;
        border-radius: 10px;

        .ui-slider-range {
          height: 4px;

          position: absolute;
          z-index: 1;

          border: none;
          background: $gray3;
        }
      }

      .ui-slider-handle {
        width: 16px;
        height: 16px;
        margin-left: -6px;

        position: absolute;
        top: -7px;

        cursor: pointer;
        background: $gray3;
        z-index: 2;

        border-radius: 100px;

        &:focus {
          outline: none;
        }
      }

      .polzunok-input-5-left,
      .polzunok-input-5-right {
        display: inline-block;
        width: 150px;
        height: 40px;
        padding: 10px 10px 10px 40px;

        font-size: 16px;
        line-height: 160%;
        background: $white;
        border: 1px solid $gray8;
        border-radius: 0;
        color: $black2;
        background: $white;

        &:focus {
          border-color: $active;
        }
      }

      .polzunok-input-5-left {
        left: 0;
      }

      .polzunok-input-5-right {
        right: 0;

      }
    }
  }

  &__all {
    span {
      font-size: 13px;
      line-height: 140%;
      letter-spacing: 0.01em;
      color: $active;
      cursor: pointer;

      &:hover {
        opacity: 0.7;
      }

      &.show {
        display: block;
      }

      &.hide {
        display: none;
      }
    }

    &.open {
      span {
        &.show {
          display: none;
        }

        &.hide {
          display: block;
          margin-top: 10px;
        }
      }
    }
  }

  &__group {
    &-check {
      position: relative;
      height: 125px;

      overflow: hidden;
    }
  }

  &__check {
    margin-bottom: 12px;

    position: relative;

    &:last-of-type {
      margin-bottom: 0;
    }

    .filter__group-check.open & {
      display: block;
    }
  }

  &__list {

    ul {
      padding-left: 20px;
      margin-bottom: 0;

      li {
        margin-bottom: 12px;

        &:last-of-type {
          margin-bottom: 0;
        }

        a {
          font-size: 14px;
          line-height: 140%;
          letter-spacing: 0.01em;
          color: $gray3;

          &:hover {
            color: $active;
          }
        }

        &.active {
          a {
            font-weight: 700;
            color: $active;
          }
        }
      }
    }
  }
}

input {
  &[type=checkbox],
  &[type=radio] {
    display: none;

    & + label {
      padding-left: 40px;

      font-size: 16px;
      line-height: 160%;
      letter-spacing: 0.01em;
      color: $black2;
      cursor: pointer;
      transition: 0.3s;

      a {
        color: $active;

        &:hover {
          color: $black2;
        }
      }

      &:before {
        content: "";
        width: 20px;
        height: 20px;
        display: inline-block;
        margin: -5px 5px 0 0;

        position: absolute;
        top: 6px;
        left: 0;

        border: 1px solid $gray8;
        background-color: transparent;
        text-align: center;
        vertical-align: middle;
        transition: color ease .3s;
        border-radius: 0;
      }

      &:hover {
        color: $gray8;
      }
    }
  }

  &:checked  {
    & + label {
      &:before {
        background-color: $gray8;
        background-image: url("../images/svg/check.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: 12px 12px;
        border-color: $gray8;
      }
    }
  }
}

