      width: 18px;
      height: 18px;
    }
  }
}

.promo {
  width: 390px;

  position: relative;

  input {
    width: 100%;
    height: 50px;
    padding: 5px 70px 5px 20px;

    font-size: 14px;
    line-height: 140%;
    color: $black;
    border: 1px solid $active;
  }

  &-check {
    display: flex;
    align-items: center;
    font-size: 12px;
    line-height: 160%;
    color: $gray3;
    margin-top: 10px;
    
    &:before {
      content: '';
      width: 12px;
      height: 12px;
      margin-right: 5px;
      
      background-repeat: no-repeat;
      background-size: 12px 12px;
      background-image: url('../images/svg/check3.svg');
    }
  }

  &-btn {
    width: 50px;
    height: 50px;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;

    position: absolute;
    right: 0;
    top: 0;
    z-index: 3;

    background: $active;
    cursor: pointer;
    border: none;
    outline: none!important;
    transition: 0.3s;

    svg {
      width: 16px;
      height: 16px;

      transform: rotate(-90deg);
      transition: 0.3s;

      use {
        color: $white;
      }
    }

    &:hover {
      background: $color10;

      svg {
        use {
          color: $gray3;
        }
      }
    }

  }
}

