          max-width: 50%;
          flex: 0 0 50%;
          padding-left: 35px;
          display: none;
          margin-bottom: 17px;

          position: relative;

          img {
            position: absolute;
            left: 0;
            top: -3px;
          }

          &:nth-of-type(1),
          &:nth-of-type(2),
          &:nth-of-type(3),
          &:nth-of-type(4),
          &:nth-of-type(5),
          &:nth-of-type(6) {
            display: block;
          }

          .open & {
            display: block;
          }

          &:hover {
            .cart-product__content-list-drop {
              display: block;
            }
          }
        }

        &-drop {
          width: 360px;
          padding: 25px;
          display: none;

          position: absolute;
          top: 40px;
          left: 10px;
          z-index: 5;

          background: $white;
          border: 1px solid rgba(230, 151, 150, 0.2);

          &:after {
            content: '';
            width: 100%;
            height: 30px;

            position: absolute;
            top: -20px;
            left: 0;
            z-index: 2;

            background: transparent;
          }

          &:before {
            content: '';
            width: 22px;
            height: 16px;
            display: block;

            position: absolute;
            top: -16px;
            left: -1px;
            z-index: 2;

            background-image: url("../images/ar.png");
            background-repeat: no-repeat;
            background-size: 22px 16px;
          }

          &-tx {
            font-size: 14px;
            line-height: 160%;
            color: $gray15;
          }

          &-link {
            margin-top: 10px;
            display: table;

            font-size: 13px;
            line-height: 150%;
            color: $gray3;

            &:hover {
              color: $active;
            }
          }
        }

        &-btn {
          display: flex;
          align-items: center;

          .op,
          .cl {
            font-weight: 500;
            font-size: 16px;
            line-height: 160%;
            color: $gray2;

            cursor: pointer;
            transition: 0.3s;

            &:after {
              content: '';
              width: 16px;
              height: 16px;
              display: inline-block;
              margin-left: 10px;

              position: relative;
              top: 2px;

              background-repeat: no-repeat;
              background-size: 16px 16px;
            }

            &:hover {
              opacity: 0.8;
            }
          }

          .op {
            display: block;

            .open & {
              display: none;
            }

            &:after {
                background-image: url("../images/svg/plus.svg");
            }
          }

          .cl {
            display: none;

            .open & {
              display: block;
            }

            &:after {
              background-image: url("../images/svg/minus.svg");
            }
          }
        }
      }

      &-rat {
        margin-top: 20px;
        padding: 30px 38px 35px;

        text-align: center;
        border: 1px solid $gray;

        &-inner {
