  &-order {
    margin-bottom: 85px;

    &__inner {
      display: flex;
      align-items: flex-start;
    }

    &__list {
      &-btn {
        padding: 43px 0 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        border-top: 1px solid rgba(236, 230, 230, 0.5);
      }
    }

    &__total {
      width: 315px;
      padding: 20px 30px 0;

      background: $white;
      box-shadow: 0px 4px 21px rgba(148, 24, 24, 0.03);

      &-item {
        margin-bottom: 22px;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;

        &.all {
          padding: 35px 0;
          margin-top: 38px;
          margin-bottom: 0;

          align-items: center;
          border-top: 1px solid rgba(0,0,0,0.1);
        }
      }

      &-text {
        font-size: 14px;
        line-height: 140%;
        color: $black2;

        .all & {
          font-weight: 600;
          font-size: 14px;
          line-height: 140%;
        }
      }

      &-price {
        text-align: right;

        &-pr {
          font-weight: 600;
          font-size: 14px;
          line-height: 140%;
          color: $black2;

          &.tw {
            &:after {
              display: none;
            }
          }

          &:after {
            display: inline-block;
            margin-left: 7px;
            font-family: $font-family2;
            content: "\20cf";
          }

          .all & {
            font-weight: 700;
            font-size: 26px;
            line-height: 110%;
            color: $black2;
            font-family: $font-family3;
          }
        }

        &-min {
          font-size: 14px;
          line-height: 125%;
          color: $black2;
        }
      }
    }

    .accordion {
      margin-right: 40px;
      width: calc(100% - 315px);
    }

    .card {
      margin-bottom: 30px;

      border-radius: 0;
      border: none!important;
      box-shadow: 0px 4px 21px rgba(148, 24, 24, 0.03);

      &-body {
        padding: 0;

        border: none!important;

        &-inner {
          padding: 20px 30px;
        }
      }

      &-header {
        padding: 0;

        border: none;

        .btn-link {
          width: 100%;
          display: flex;
          align-items: center;
          padding: 20px 30px;

          background: $white;
          border-radius: 0!important;
          border: 0!important;

          &[aria-expanded="true"] {
            background: $white;
            border-bottom: 1px solid $black;

            .step {
              background: $gray8;
            }
          }

          &[aria-expanded="false"] {
            border: 1px solid transparent;
          }

          &:hover,
          &:focus{
            text-decoration: none!important;
          }

          .step {
            width: 40px;
            height: 40px;

            display: inline-flex;
            align-items: center;
            justify-content: center;

            border-radius: 100px;
            background: $gray8;

            span {
              display: block;
              font-weight: 500;
              font-size: 16px;
              line-height: 190%;
              color: $white;
              text-transform: uppercase;
            }
          }

          .title {
            padding-left: 30px;
            width: calc(100% - 130px);
            font-weight: 600;
            font-size: 14px;
            line-height: 17px;
            letter-spacing: 0.03em;
            text-transform: uppercase;
            color: $gray3;
          }

          .change {
            display: none;
          }

        }
      }

      &.done {
        .cart-order__list-btn {
          display: none;
        }

        .btn {
          //padding-bottom: 0!important;
          border: none!important;
        }

        .step {
          background-image: url("../images/svg/check.svg")!important;
          background-size: 16px 16px!important;
          background-repeat: no-repeat!important;
          background-position: center!important;

          background-color: $active!important;

          span {
            display: none;
          }
        }

        .change {
          display: block;
          padding-left: 20px;
          min-height: 16px;

          position: absolute;
          right: 35px;

          font-size: 14px;
          line-height: 140%;
          text-align: center;
          color: $black;

          &:before {
            content: '';
            display: block;
            width: 16px;
            height: 16px;

            position: absolute;
            top: -2px;
            left: 0;

            background-image: url("../images/svg/edit.svg");
            background-size: 16px 16px;
            background-repeat: no-repeat;
          }

          &:hover {
            opacity: 0.7;
          }
        }
      }
    }

    &__bt {
      max-width: 530px;
      padding: 0;

      position: relative;

      input:checked + label:before {
        background-color: $active;
        border-color: $active;
      }

      label {
        display: block;
        margin-bottom: 40px;

        font-size: 12px!important;
        line-height: 160%!important;
        color: $gray3!important;

        position: relative;

        a {
          color: $gray3!important;
          font-weight: 700;
          text-decoration: none;

          &:hover {
            color: $active!important;
          }
        }
      }

      p {
        margin-bottom: 60px;

        font-size: 14px;
        line-height: 140%;

        a {
          color: $active;

          &:hover {
            color: $active;
          }
        }
      }

      .button {
        width: 270px;

        text-align: center;
      }
    }

    &__delivery {
      padding: 20px 0 40px;

      &-tt {
        margin-bottom: 30px;

        font-weight: 600;
        font-size: 16px;
        line-height: 140%;
        color: $gray15;
      }

      &-inner {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
      }

      &-group {
