            height: 165px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin: 0;
            padding: 20px;

            position: relative;

            border: 1px solid $gray8;
            border-radius: 0;
            cursor: pointer;

            img {
              margin: 20px auto;
              display: block;
              max-width: 100%;
              max-height: 140px;
            }

            .tit {
              display: block;
              padding-right: 20px;
              min-height: 60px;

              position: absolute;
              bottom: -80px;
              left: 0;
              z-index: 2;

              font-size: 16px;
              line-height: 160%;
              color: $gray18;
            }

            .price {
              display: none;

              padding: 7px 20px;
              bottom: -10px;
            }

            a {
              color: $active;

              &:hover {
                opacity: 0.8;
              }
            }

            &:before {
              content: "";
              width: 21px;
              height: 21px;
              display: inline-block;
              margin: -5px 5px 0 0;

              position: absolute;
              top: 25px;
              left: 20px;
              right: inherit;

              border: 1px solid $gray8;
              background-color: transparent;
              text-align: center;
              vertical-align: middle;
              transition: color ease .3s;
              border-radius: 0;
            }
          }

          &:checked  {
            & + label {
              &:before {
                background-color: $active;
                background-image: url("../images/svg/check.svg");
                background-repeat: no-repeat;
                background-position: center;
                background-size: 12px 12px;
                border-color: $active;
              }
            }
          }
        }

      }

      &-choice {
        width: 320px;
        padding: 30px;

        background: $bg3;

        p {
          margin-bottom: 28px;

          font-size: 14px;
          line-height: 140%;
          letter-spacing: 0.01em;
          color: $black;

          a {
            color: $active;

            &:hover {
              color: $active;
            }
          }
        }

        &-img {
          margin-bottom: 23px;
          display: flex;
          align-items: center;
          justify-content: center;
          height: 133px;
          background: $white;

          img {
            max-height: 100px;
            max-width: 100%;
          }
        }

        &-title {
