.amount {
  width: 140px;
  margin: 0 20px;

  position: relative;

  &:before {
    content: '';
    width: 100%;
    height: 100%;

    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;

    background: transparent;
  }

  input {
    width: 100%;
    height: 50px;
    padding: 5px 40px;

    border: 1px solid $gray13;
    background: $white;
    font-size: 18px;
    line-height: 150%;
    color: $gray3;
    text-align: center;
  }

  .up,
  .down {
    width: 20px;
    height: 20px;

    display: flex;
    align-items: center;
    justify-content: center;

    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    z-index: 3;

    cursor: pointer;

    &:before {
      content: '';
      width: 10px;
      height: 2px;
      display: block;

      border-radius: 2px;
      background: $gray12;
    }
  }

  .down {
    left: 15px;
  }

  .up {
    right: 15px;

    &:after {
      content: '';
      width: 2px;
      height: 10px;
      display: block;

      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;

      border-radius: 2px;
      background: $gray12;
    }
  }
}

